.main-form {
  width: 100%;
  max-width: 820px;
  padding: 40px;
  background-color: var(--white);

  &:not(.main-form--modal) {
    padding: 40px 60px 25px 90px;

    @include m-b-laptop-down {
      padding: 40px;
    }

    @include m-b-mobile-lg-down {
      padding: 30px 20px;
    }
  }

  @include m-b-laptop-down {
    padding: 40px;
  }

  @include m-b-mobile-lg-down {
    padding: 30px 0;
  }

  &__title {
    display: inline-block;
    font-family: var(--alt-font-family);
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 0.2px;
    margin-bottom: 30px;

    @include m-b-laptop-down {
      font-size: 24px;
      margin-bottom: 20px;
    }

    @include m-b-mobile-lg-down {
      font-size: 18px;
      margin-bottom: 15px;
    }
  }

  form {
    @extend .flex-column;
  }

  label {
    font-family: var(--alt-font-family);
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0.2px;
    margin-bottom: 8px;

    span {
      color: var(--accent);
    }
  }

  input,
  textarea {
    padding: 10px;
    border: 1px solid #bdbdbd;
    background-color:  var(--light);
  }

  input:not(input[type="submit"]) {
    margin-bottom: 16px;
    min-height: 50px;

    @include m-b-laptop-down {
      min-height: 40px;
    }

    @include m-b-mobile-lg-down {
      margin-bottom: 12px;
    }

    &::placeholder {
      font-size: 16px;
      font-family: var(--alt-font-family);
      color: var(--primary);
    }
  }

  textarea {
    width: 100%;
    height: 110px;
    resize: none;
    margin-bottom: 16px;
  }

  *[type="submit"] {
    width: 100%;
    max-width: 480px;
    margin: 16px auto 18px;
    min-height: 58px;
    background-color: var(--accent);

    @include m-b-tablet-down {
      min-height: 48px;
    }
  }

  small {
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.2px;
    text-align: center;
    color: var(--gray);

    a {
      font-weight: 700;
      color: var(--gray);
      @extend .hover-opacity;
    }
  }
}

.main-form.main-form--subscribe {
  padding: 0;

  input {
    max-width: 480px;
  }

  input[type="submit"] {
    margin: 16px 0;
  }

  small {
    text-align: left;
  }
}

.control-wrapper {
  @extend .flex-column;
  @extend .fieldset-reset;

  input {
    width: 100%;
    transition: border-color var(--main-transition);
  }
}

.phone-field {
  width: 100%;
}

.invalid-field input {
  border-color: var(--accent);
  animation: shaker .7s ease-in;
}

@keyframes shaker {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-5px);
  }

  50% {
    transform: translateX(5px);
  }

  75% {
    transform: translateX(-5px);
  }

  100% {
    transform: translateX(0);
  }
}
