/*   */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 199;
  display: none;
}
.modal.active {
  display: block;
}
.modal-closer {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
}
.modal-closer svg {
  fill: var(--primary);
}
.modal__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal__content {
  position: relative;
  width: 90vw;
  max-width: 450px;
  height: fit-content;
  max-height: 85vh;
  padding: 25px;
  overflow: hidden;
  overflow-y: auto;
  background-color: var(--white);
  background-image: url("../assets/img/offer-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
.modal__content-body {
  align-items: center;
}
.modal__content-header {
  margin-bottom: 30px;
}
.modal__content-header span {
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--white);
}
.modal-form-answer {
  display: block;
  padding: 35px 0;
}
.modal--success .modal-form-answer {
  padding: 0;
}

.staff-card-modal .modal__content {
  padding: 100px;
  width: 90vw;
  max-width: 1240px;
}
@media (max-width: 1240px) {
  .staff-card-modal .modal__content {
    padding: 40px;
  }
}
@media (max-width: 960px) {
  .staff-card-modal .modal__content {
    padding: 40px 20px;
  }
}
@media (max-width: 767px) {
  .staff-card-modal .modal__content {
    max-width: 400px;
    padding: 0;
    padding-bottom: 30px;
  }
}
@media (min-width: 768px) {
  .staff-card-modal .modal__content:hover img, .staff-card-modal .modal__content:focus img {
    -webkit-filter: grayscale(0);
    filter: grayscale(0);
    transform: scale(1.03);
  }
}