.hero {
  position: relative;
  height: 100vh;
  min-height: 768px;
  max-height: 900px;
  background-color: var(--light);

  @include m-b-desktop-down {
    min-height: 634px;
    max-height: 768px;
  }

  @include m-b-laptop-down {
    min-height: 534px;
    max-height: 668px;
  }

  @include m-b-mobile-lg-down {
    min-height: 420px;
    max-height: 520px;
  }

  &__container {
    position: relative;
    @extend .flex-column;
    justify-content: center;
    width: 100%;
    max-width: 1400px;
    height: 100%;
    z-index: 2;
    padding: 0 80px;

    @include m-b-desktop-down {
      padding: 35px;
    }

    @include m-b-mobile-lg-down {
      padding: 20px;
      padding-top: 64px;
    }
  }

  &__intro {
    display: inline-block;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 10px;
    color: var(--white);

    @include m-b-tablet-down {
      font-size: 18px;
    }

    @include m-b-mobile-lg-down {
      font-size: 14px;
    }
  }

  &__title {
    font-family: var(--alt-font-family);
    font-size: 64px;
    font-weight: 700;
    margin: 0;
    margin-bottom: 20px;
    color: var(--white);

    @include m-b-desktop-down {
      font-size: 64px;
    }

    @include m-b-laptop-down {
      font-size: 54px;
    }

    @include m-b-tablet-down {
      font-size: 48px;
    }

    @include m-b-mobile-lg-down {
      font-size: 32px;
    }
  }

  &__text {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 45px;
    color: var(--white);

    @include m-b-laptop-down {
      font-size: 24px;
    }

    @include m-b-mobile-lg-down {
      font-size: 16px;
      max-width: 380px;
    }
  }

  button {
    max-width: 210px;
    min-height: 66px;
    color: var(--accent);
    background-color: var(--white);

    @include m-b-desktop-down {
      min-height: 54px;
    }

    @include m-b-mobile-lg-down {
      margin-top: auto;
      min-height: 48px;
    }
  }
}

.hero-section-swiper {
  width: 100%;
  height: 100%;

  .swiper-slide {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;

    &::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      content: '';
      background: linear-gradient(90deg, rgba(31, 31, 31, 0.60) 0%, rgba(255, 255, 255, 0.20) 100%);
      z-index: 1;
    }
  }

  .swiper-slide-content {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    margin: auto;

  }
}
